.header {
	width: 100%;
	height: 104px;
	position: fixed;
	top: 0;
	z-index: 97;

	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding: 36px 0;
	}
}

@media (max-width: 690px) {
	.header {
		height: 80px;

		&__inner {
			padding: 25px 15px;
		}
	}
}